
import {defineComponent, PropType} from "vue"
import Contact from "@/classes/chat/Contact";
export default defineComponent({
    name: 'ContactItem',
    props: {
        contact: {
            type: Object as PropType<Contact>,
            required: true
        }
    },
    methods: {
        onClick() {
            this.$router.push({name: 'chat', params: {chatId: this.contact.uid}})
        }
    }
})
